import React, {Component} from "react";
import PropTypes from "prop-types";
import OnlineUtils from "eshop/utils/OnlineUtils";

var floatingLabelSelectId = 0;

export class OraFloatingLabelSelect extends Component {
    constructor(props) {
        super(props);
        this.handleChange = this.handleChange.bind(this)
        this.handleChangeIe8 = this.handleChangeIe8.bind(this)
    }

    componentDidMount() {
        if (this.props.floating) {
            this.loadModule();
        }
        if (OnlineUtils.ifIe8) {
            let select = document.getElementById(this.props.id);
            select.attachEvent("onchange", this.handleChangeIe8.bind(this));
        }
    }

    componentDidUpdate() {
        if (this.props.isFeedbackParam) {
            if (typeof Feedback !== 'undefined') {
                Feedback.showHideFeedback();
            }
        }
    }

    loadModule() {
        OPL.UI.loadModules(this.ref, [{
            path: 'core/modules/floating-label',
            options: {}
        }]);
    }

    handleChangeIe8(e) {
        let {id, name, value} = e.srcElement;
        this.props.onChange({id, name, value});
    }

    handleChange(event) {
        let index = this.props.index;
        let {id, name, value} = event.target;
        if(index !== undefined){
            this.props.onChange({id, name, value, index});
        }else {
            this.props.onChange({id, name, value});
        }
    }

    getError() {
        if(this.props.errors && this.props.errors.length > 0){
            return this.props.errors[0];
        }
        return null;
    }

    getSelectClasses() {
        let disabled = this.props.disabled || this.props.options.length === 1;
        let disabledClass = disabled ? ' g-gray6-c' : '';
        let selectedClass = !!this.props.selected ? ' is-not-empty success' : '';
        let primaryClass = this.props.isPrimary ? ' opl-select-primary' : '';
        return 'u-padding-right-xl is-not-empty ' + this.props.className + selectedClass + primaryClass + disabledClass;
    }

    getSelectArrowClasses() {
        return 'o-select__arrow ' + this.getBackgroundColorFromClassName();
    }

    getBackgroundColorFromClassName(){
        let className=this.props.className;
        const regex = /\S*-bg/g;
        let resultColor=className.match(regex);
        return resultColor&&resultColor.length===1 ? resultColor : "g-white1-bg";
    }

    render() {
        let error = this.getError.apply(this);
        return (
            <div className="">
                <div ref={ref => this.ref = ref} className="o-floating-label o-select">
                    <select onChange={this.handleChange}
                            name={this.props.name}
                            value={this.props.selected}
                            id={this.props.id}
                            disabled={this.props.disabled || this.props.options.length === 1}
                            data-feedback-param={this.props.isFeedbackParam}
                            className={this.getSelectClasses.apply(this)}
                            style={this.props.style}
                            aria-describedby={"floating-label__placeholder_" + this.props.id}>
                        {this.props.withEmptyOption ?
                            <option key={this.props.emptyOption.value} value={this.props.emptyOption.value}>
                                {this.props.emptyOption.description}</option>
                            : null}
                        {this.props.options.map(
                            function (option) {
                                return <option key={option.value} value={option.value} hidden={option.hidden}
                                               disabled={option.disabled}>{option.description}</option>
                            }
                        )}
                    </select>
                    {!this.props.disabled && <span className={this.getSelectArrowClasses.apply(this)}></span>}
                    <p id={"floating-label__placeholder_" + this.props.id}
                       className={"label o-floating-label__placeholder u-padding-right-l"}>{this.props.label}</p>
                </div>
                {error && <div id={this.props.id} className={'opl-msg opl-msg--context u-margin-top-s opl-msg--' + error.level}>
                    <p>{error.message}</p>
                </div>}
            </div>
        );
    }
}

OraFloatingLabelSelect.propTypes = {
    id: PropTypes.string.isRequired,
    options: PropTypes.arrayOf(PropTypes.shape({
        value: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
        description: PropTypes.string
    })),
    selected: PropTypes.oneOfType([PropTypes.string,PropTypes.number]),
    disabled: PropTypes.bool,
    className: PropTypes.string,
    btnClassName: PropTypes.string,
    onChange: PropTypes.func,
    emptyOption: PropTypes.shape({
        value: PropTypes.string,
        description: PropTypes.string,
        hidden: PropTypes.bool,
        disabled: PropTypes.bool
    }),
    withEmptyOption: PropTypes.bool,
    singleDataText: PropTypes.string,
    floating: PropTypes.bool,
    isPrimary: PropTypes.bool
};

OraFloatingLabelSelect.defaultProps = {
    emptyOption: {value: "", description: "Wybierz..."},
    withEmptyOption: false,
    floating: false,
    isPrimary: true
};
